import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import APIcon from '../APIcon/APIcon';
import './APIconButton.css';

const APIconButton = props => {
  const {
    tooltipText,
    iconName,
    customClass,
    action,
    showLabel,
    tooltipPosition
  } = props;
  const baseClass = 'icon-button';
  const bemClasses = getBEMClasses(baseClass, customClass);

  return (
    <React.Fragment>
      <div
        tooltip={tooltipText ? tooltipText : null}
        tooltip-position={tooltipPosition}
        onClick={action}
        className={bemClasses()}>
        <APIcon name={iconName} customClass={bemClasses('icon')} />
        {showLabel && <span className={bemClasses('label')}>{iconName}</span>}
      </div>
    </React.Fragment>
  );
};

APIconButton.defaultProps = {
  showLabel: true,
  tooltipPosition: 'top'
};

APIconButton.protoTypes = {
  iconName: PropTypes.string.isRequired,
  parentClass: PropTypes.string.isRequired,
  tooltipPosition: PropTypes.string,
  tooltipText: PropTypes.string,
  action: PropTypes.func,
  showLabel: PropTypes.bool
};

export default APIconButton;



