import { createSelector } from 'reselect';
import config from '../../config';

export const productOptionsSelector = createSelector(
  [state => state.product.products],
  products => {
    let isMisconfigured = false;

    const productsOptions = (config.BaseCurrencies || []).map(currency => {
      const product = products.find(p => p.Product === currency);

      isMisconfigured = isMisconfigured || !product;

      return isMisconfigured
        ? null
        : {
            value: currency,
            label: product.ProductFullName
          };
    });

    return {
      productsOptions,
      isMisconfigured
    };
  }
);

const instrumentHasProductId = prodId => ins =>
  ins.Product1 === prodId || ins.Product2 === prodId;

const getInstrumentsById = (instruments, id) =>
  instruments.filter(instrumentHasProductId(id));

const findInstrumentsById = (instruments, id) =>
  instruments.find(instrumentHasProductId(id));

const findInstrumentLevel = (instruments, level1, id) => {
  const instrument = findInstrumentsById(instruments, id);

  if (instrument) {
    const level = level1[instrument.InstrumentId];
    return level && { instrument, level };
  }
};

const getValueById = (inst, level, id) =>
  inst.Product1 === id ? level.LastTradedPx : 1 / level.LastTradedPx;

export const baseCurrencyPriceSelector = createSelector(
  [
    state => state.level1,
    state => state.instrument.instruments,
    state => state.product.products,
    state => state.user.userConfig,
    (state, productId) => productId
  ],
  (level1, instruments, products, userConfig, productId) => {
    let baseCurrency = userConfig.find(config => config.Key === 'BaseCurrency');

    if (!baseCurrency) return;

    baseCurrency = baseCurrency.Value;

    // Case:
    // * BaseCurrency is the same as the productId
    const product = products.find(p => p.ProductId === productId);
    const baseProduct = products.find(p => p.Product === baseCurrency);
    if (product === baseProduct) return;

    const baseProductId = baseProduct.ProductId;

    const productInstruments = getInstrumentsById(instruments, productId);

    // Case:
    // * BaseCurrency is on level 1 on the same instrument as the productId
    const sharedInstrument = findInstrumentsById(
      productInstruments,
      baseProductId
    );

    if (sharedInstrument) {
      const level = level1[sharedInstrument.InstrumentId];

      if (!level) return;

      const value = getValueById(sharedInstrument, level, productId);
      return { ...baseProduct, value };
    }

    /* Case:
    ** BaseCurrency is on level1 but on another instrument **
    For this we need two instruments these should have a product that is paired with the current
    product and the baseCurrency but also these instruments need to exist on level1
     */
    const baseCurrencyInstruments = getInstrumentsById(
      instruments,
      baseProductId
    );

    let result;
    products.forEach(({ ProductId }) => {
      if (result) return;

      const baseObj = findInstrumentLevel(
        baseCurrencyInstruments,
        level1,
        ProductId
      );
      const prodOb = findInstrumentLevel(productInstruments, level1, ProductId);

      if (baseObj && prodOb) {
        result = {
          id: ProductId,
          base: baseObj,
          product: prodOb
        };
      }
    });
    // This means there's no level available for either of product or baseCurrency
    if (!result) return;

    const proxyProductValue = getValueById(
      result.product.instrument,
      result.product.level,
      result.id
    );
    const proxyBaseCurrencyValue = getValueById(
      result.base.instrument,
      result.base.level,
      result.id
    );

    const value = proxyBaseCurrencyValue / proxyProductValue;
    return { ...baseProduct, value };
  }
);



